import http from '@/api/http.js'
export default{
	data(){
		return{
			priGoods:['特权商品','礼包商品'],
			current:0,
			list:{},
			tequan:[],
			type:1,
			userInfo:{},
			page:1
		}
	},
	onLoad() {
		this.getList()
		this.getUserInfo()
	},
	onReachBottom() {
		this.getNewList()
	},
	methods:{
		changePri(index){
			this.current = index
			this.getList()
		},
		toDetail(id,catty){
			// if(catty > 0){
			// 	uni.navigateTo({
			// 		url:'/pages/index/detail?goodsid='+ id + '&tequan=tequan'
			// 	})
			// }else{
			// 	this.$util.showToast('该商品库存不足')
			// 	return
			// }
			uni.navigateTo({
				url:'/pages/index/detail?goodsid='+ id + '&tequan=tequan'
			})
		},
		toGiftDetail(id,catty){
			uni.navigateTo({
				url:'/pages/index/giftDetail?goodsid='+ id +'&gift=gift'
			})
		},
		getUserInfo(){
			let data = {
				token:uni.getStorageSync('token')
			}
			http('/api/User/get_userinfo','post',data).then(res=>{
				if(res.data.status == '200'){
					this.userInfo = res.data.data
				}
				console.log(res)
			})
		},
		getList(){
			let that = this
			if(that.current == 0){
				that.type = 1
			}else if(that.current == 1){
				that.type = 2
			}
			let data = {
				token:uni.getStorageSync('token'),
				type:that.type,
				page:1,
				pagesize:10
			}
			http('/api/user/getMemberGoods','post',data).then(res=>{
				if(res.data.status == '200'){
					that.list = res.data.data
					that.tequan = res.data.data.goodsLists
				}
				console.log(res)
			})
		},
		getNewList(){
			if(this.current == 0){
				this.type = 1
			}else if(this.current == 1){
				this.type = 2
			}
			let data = {
				token:uni.getStorageSync('token'),
				page:this.page+1,
				type:this.type
			}
			http('/api/user/getMemberGoods','post',data).then(res=>{
				if(res.data.status=='400'){
					this.$util.showToast(res.data.msg)
					return
				}
				
				// if(this.tequan.length == res.data.data.goodsLists.length){
				// 	// this.$util.showToast('没有更多了')	
				// 	return false
				// }
				this.page++
				this.tequan = this.tequan.concat(res.data.data.goodsLists)
			})
		}
	}
}
